
























































































































































import { Component, Prop, Emit, Vue } from "vue-property-decorator";

@Component
export default class MaCalcModal extends Vue {
  @Prop({ default: 0 })
  private initialValue!: number;

  private monthlyAverage: number = 0;
  private firstMonth: number = this.initialValue;
  private secondMonth: number = this.initialValue;
  private thirdMonth: number = this.initialValue;
  private fourMoth: number = this.initialValue;
  private fiveMonth: number = this.initialValue;
  private sixMonth: number = this.initialValue;
  private sevenMonth: number = this.initialValue;
  private eightMonth: number = this.initialValue;
  private nineMonth: number = this.initialValue;
  private tenMonth: number = this.initialValue;
  private elevenMonth: number = this.initialValue;
  private twelveMonth: number = this.initialValue;

  protected onClickInputButton() {
    this.firstMonth = this.initialValue;
    this.secondMonth = this.initialValue;
    this.thirdMonth = this.initialValue;
    this.fourMoth = this.initialValue;
    this.fiveMonth = this.initialValue;
    this.sixMonth = this.initialValue;
    this.sevenMonth = this.initialValue;
    this.eightMonth = this.initialValue;
    this.nineMonth = this.initialValue;
    this.tenMonth = this.initialValue;
    this.elevenMonth = this.initialValue;
    this.twelveMonth = this.initialValue;
  }

  protected resetModal() {
    this.monthlyAverage = 0;
  }

  protected handleOk(bvModalEvt: any) {
    // Prevent modal from closing
    bvModalEvt.preventDefault();
    // Trigger submit handler
    this.handleSubmit();
    this.ok(this.monthlyAverage);
  }

  protected handleSubmit() {
    // 
    this.monthlyAverage = Math.floor(
      (this.firstMonth +
        this.secondMonth +
        this.thirdMonth +
        this.fourMoth +
        this.fiveMonth +
        this.sixMonth +
        this.sevenMonth +
        this.eightMonth +
        this.nineMonth +
        this.tenMonth +
        this.elevenMonth +
        this.twelveMonth) /
        12
    );

    // Hide the modal manually
    this.$nextTick(() => {
      // this.$refs.modal.hide();
      this.$bvModal.hide("modal-scrollable");
    });
  }

  @Emit()
  private ok(monthlyAverage: number) {
    // do nothing
  }
}
