












































































































































































import { Component, Prop, Emit, Vue } from "vue-property-decorator";
import MaCalcModal from "@/components/MaCalcModal.vue";
import MaSixMonthTotalSalaryModal from "@/components/MaSixMonthTotalSalaryModal.vue";
import MaternityAllowanceHelper from "@/helper/MaternityAllowanceHelper";

@Component({
  components: {
    MaCalcModal,
    MaSixMonthTotalSalaryModal
  }
})
export default class MaternityAllowance extends Vue {
  private sixMonthOfSalary: number = 0;
  private standardMonthlyRemuneration: number = 0;
  private maternityLeaveStartDate: string = "2019-01-01";
  private childcareLeaveEndDate: string = "2020-04-30";
  private expectedDateOfBirth: string = "2019-01-01";

  protected onMaCalcModalClicked(monthlyAverage: number) {
    this.standardMonthlyRemuneration = monthlyAverage;
  }

  protected onMaSixMonthTotalSalaryModalClicked(total: number) {
    this.sixMonthOfSalary = total;
  }

  get averageMonthlyRemuneration(): number {
    return this.standardMonthlyRemuneration;
  }

  get startDateOfMaternityLeave(): Date {
    return new Date(this.maternityLeaveStartDate);
  }

  get endDateOfMaternityLeave(): Date {
    return MaternityAllowanceHelper.endDateOfMaternityLeave(
      this.startDateOfMaternityLeave,
      new Date(this.expectedDateOfBirth)
    );
  }

  get maternityAllowanceFee(): number {
    return MaternityAllowanceHelper.totalMaternityAllowanceFee(
      this.standardMonthlyRemuneration,
      this.startDateOfMaternityLeave,
      new Date(this.expectedDateOfBirth)
    );
  }

  // 
  get startDateOfChildcareLeave(): Date {
    const startDateOfChildcareLeave = new Date(
      this.endDateOfMaternityLeave.toString()
    );

    startDateOfChildcareLeave.setDate(
      this.endDateOfMaternityLeave.getDate() + 1
    );

    return startDateOfChildcareLeave;
  }

  // 
  get endDateOfChildcareLeave(): Date {
    return new Date(this.childcareLeaveEndDate);
  }

  get under180MonthlyChildcareLeaveBenefits(): number {
    // 180300.67;
    // 181300.5
    return Math.floor(this.childcareLeaveDailyAmount() * 30 * 0.67);
  }

  get over180MonthlyChildcareLeaveBenefits(): number {
    // 181300.5
    return Math.floor(this.childcareLeaveDailyAmount() * 30 * 0.5);
  }

  get totalChildcareLeaveBenefits(): number {
    // 
    // 
    const diffTime =
      this.endDateOfChildcareLeave.getTime() -
      this.startDateOfChildcareLeave.getTime();
    const diffDay = Math.floor(diffTime / (1000 * 60 * 60 * 24));
    const childCareLeavePeriod = diffDay + 1;

    // 180
    if (childCareLeavePeriod <= 180) {
      return this.childcareLeaveDailyAmount() * childCareLeavePeriod * 0.67;
    }
    const under180DayChildcareLeaveBenefits = Math.floor(
      this.childcareLeaveDailyAmount() * 180 * 0.67
    );
    const over180DayChildcareLeaveBenefits = Math.floor(
      this.childcareLeaveDailyAmount() * (childCareLeavePeriod - 180) * 0.5
    );
    return under180DayChildcareLeaveBenefits + over180DayChildcareLeaveBenefits;
  }

  private childcareLeaveDailyAmount(): number {
    // 

    //       
    // 
    // 180
    // 
    // 
    // 

    // 180
    // 
    let dailyAmount = Math.floor(this.sixMonthOfSalary / 180);

    // 3081
    // https://jsite.mhlw.go.jp/tokyo-hellowork/content/contents/000270880.pdf
    // 
    if (dailyAmount * 30 >= 449700) {
      dailyAmount = 449700 / 30;
    }
    // 
    if (dailyAmount * 30 <= 74400) {
      dailyAmount = 74400 / 30;
    }

    return dailyAmount;
  }
}
