


























































































import { Component, Prop, Emit, Vue } from "vue-property-decorator";

@Component
export default class MaSixMonthTotalSalaryModal extends Vue {
  @Prop({ default: 0 })
  private initialValue!: number;

  private total: number = 0;
  private firstMonth: number = this.initialValue;
  private secondMonth: number = this.initialValue;
  private thirdMonth: number = this.initialValue;
  private fourMoth: number = this.initialValue;
  private fiveMonth: number = this.initialValue;
  private sixMonth: number = this.initialValue;

  protected onClickInputButton() {
    this.firstMonth = this.initialValue;
    this.secondMonth = this.initialValue;
    this.thirdMonth = this.initialValue;
    this.fourMoth = this.initialValue;
    this.fiveMonth = this.initialValue;
    this.sixMonth = this.initialValue;
  }

  protected resetModal() {
    this.total = 0;
  }

  protected handleOk(bvModalEvt: any) {
    // Prevent modal from closing
    bvModalEvt.preventDefault();
    // Trigger submit handler
    this.handleSubmit();
    this.ok(this.total);
  }

  protected handleSubmit() {
    this.total = Math.floor(
      this.firstMonth +
        this.secondMonth +
        this.thirdMonth +
        this.fourMoth +
        this.fiveMonth +
        this.sixMonth
    );

    this.$nextTick(() => {
      this.$bvModal.hide("six-month-total-salary");
    });
  }

  @Emit()
  private ok(total: number) {
    // Do nothing
  }
}
